.styled__text h2, .styles_faq-row-wrapper__3vA1D h2 {
  margin: 40px auto;
  font-family: 'Orbitron', sans-serif;
  /* font-size: 70px; */
  font-weight: bold;
  color: purple !important;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 5px;
  text-shadow: 2px 3px 7px white, 0 3px 1px white;
}
.strategy-title {
  /* margin: 40px auto; */
  font-family: 'Orbitron', sans-serif;
  /* font-size: 70px; */
  font-weight: bold;
  color: black !important;
  text-transform: uppercase;
  /* text-align: center; */
