.about-content {
  padding-right: 30px;
}
.about-gif {
  border-radius: 50%;
  width: 100%;
}
.faq-title h2 {
  color: white !important;
  /* text-shadow: 2px 3px 7px #331934, 0 3px 1px #6b6dff; */
  text-shadow: 2px 3px 7px deeppink, 0 3px 1px deeppink;
}
.button-mint:hover{
  background-color: deeppink !important;
}
@media only screen and (max-width: 760px) {
  .faq-row-wrapper {
    padding: 50px 100px;
  }
  .about-gif {
    border-radius: 50%;
    width: 50%;
  }
  .about-image {
    order: -1;
    padding: 25px 0px;
  }
}
@media only screen and (max-width: 610px) {
  .MuiTable-root {
    margin: 0 auto;
  }
  .MuiTableRow-root {
    display: flex !important;
    flex-direction: column;
  }
  .MuiTableCell-root {
    padding: 16px 0px !important;
    margin: 0 auto;
    border: 0;
  }
  th:first-child,
  td:first-child {
    margin: 0 auto;
    text-align: center;
    display: flex;
    padding: 0;
    justify-content: space-between;
    border: 0;
  }
  th:last-child,
  td:last-child {
    border: 0;
  }
}
@media only screen and (min-width: 600px) {
 
    h2,
    .h2 {
      @include font-size(h2-desktop);
    }
    .styles_faq-row-wrapper__3vA1D h2{
      font-size: 44px !important;
      margin: 0 auto !important;
      margin-bottom: 20px !important;
    }
  
  
}
@media only screen and (max-width: 512px) {
  .faq-row-wrapper {
    padding: 50px 20px;
  }
  .styled__text h2, .styles_faq-row-wrapper__3vA1D h2 {
    margin: 0 auto;
  }
  .about-content {
    padding-right: 0;
  }
  .about-gif {
    width: 60%;
  }
}
@media only screen and (max-width: 350px) {
  .button-mint {
